<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title=":!" type="warning" :closable="false" show-icon></el-alert>
      <!--  -->
      <el-row class="el-cascader">
        <el-col>
          <span>:</span>
          <el-cascader
            expandTrigger="hover"
            v-model="value"
            :options="paramsoptions"
            :props="paramsCascader"
            @change="handleChange"
            clearable
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- Tabs -->
      <el-tabs v-model="TabsName" @tab-click="TabshandleClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <!--  -->
          <el-button type="primary" size="mini" :disabled="isoptfor" @click="openDialog"></el-button>
          <!--  -->
          <el-table :data="manyData" style="width: 100%">
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag
                  v-for="(tag,index) in scope.row.attr_vals"
                  :key="index"
                  closable
                  @close="restInputConfirm(index,scope.row)"
                >{{tag}}</el-tag>
                <!-- tag -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" size="mini" @click="editopenDialog(scope.row.attr_id)"></el-button>
                <el-button type="danger" size="mini" @click="deleteOpenDialog(scope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <!--  -->
          <el-button type="primary" size="mini" :disabled="isoptfor" @click="openDialog"></el-button>
          <!--  -->
          <el-table :data="onlyData" style="width: 100%">
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag
                  v-for="(tag,index) in scope.row.attr_vals"
                  :key="index"
                  closable
                  @close="restInputConfirm(index,scope.row)"
                >{{tag}}</el-tag>
                <!-- tag -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" size="mini" @click="editopenDialog(scope.row.attr_id)"></el-button>
                <el-button type="danger" size="mini" @click="deleteOpenDialog(scope.row.attr_id)" ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog :title="''+titleText" :visible.sync="dialogVisible" width="40%" @close="resetForm">
      <!--  -->
      <el-form :model="addForm" :rules="addrulesForm" ref="addForm1" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addsubmitForm"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="dialogVisible2"
      width="40%"
      @close="resetForm2"
    >
      <!--  -->
      <el-form :model="editForm" :rules="editrulesForm" ref="addForm2" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible2 = false"> </el-button>
        <el-button type="primary" @click="editsubmitForm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created () {
    this.getParamsList()
  },
  data () {
    return {
      paramsoptions: [], // 
      value: [], // 
      paramsCascader: {
        // :props
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      TabsName: 'many', // Tabs
      manyData: [], // 
      onlyData: [], // 
      dialogVisible: false,
      dialogVisible2: false,
      addForm: {
        // 
        attr_name: ''
      },
      editForm: {
        // 
        attr_name: ''
      },
      addrulesForm: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editrulesForm: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    isoptfor () {
      // value,
      if (this.value.length !== 3) {
        return true
      } else {
        return false
      }
    },
    cascaderId () {
      // value,
      if (this.value.length !== 3) {
        return null
      } else {
        return this.value[2]
      }
    },
    titleText () {
      if (this.TabsName !== 'many') {
        return ''
      } else {
        return ''
      }
    }
  },
  methods: {
    //  
    async getParamsList () {
      const { data: res } = await this.$axios.get('categories', {
        params: { type: 3 }
      })
      if (res.meta.status !== 200) {
        this.$notify.error({
          title: '',
          message: res.meta.msg,
          duration: 2000
        })
      }
      this.paramsoptions = res.data
    },
    //  
    async gethandleChange () {
      if (this.value.length !== 3) {
        // 
        this.value = []
      }
      const { data: res } = await this.$axios.get(
        `categories/${this.cascaderId}/attributes`,
        { params: { sel: this.TabsName } }
      )
      if (res.meta.status !== 200) {
        this.$notify.error({
          title: '',
          message: '',
          duration: 2000
        })
      } else {
        res.data.forEach(item => {
          // tagv-for
          item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
          // inputvalueinputVisible
          item.inputVisible = false
          item.inputValue = ''
        })
        if (this.TabsName === 'many') {
          // 
          this.manyData = res.data // 
        } else {
          // 
          this.onlyData = res.data // 
        }
      }
    },
    // 
    handleChange () {
      if (this.value.length !== 3) {
        // 
        this.value = []
        this.manyData = []
        this.onlyData = []
      }
      this.gethandleChange() // ,
    },
    // Tabs
    TabshandleClick () {
      this.gethandleChange() // ,
      this.addForm.attr_sel = this.TabsName
    },
    // 
    openDialog () {
      this.dialogVisible = true
    },
    //  id
    async editopenDialog (attrId) {
      this.dialogVisible2 = true
      const { data: res } = await this.$axios.get(
        `categories/${this.cascaderId}/attributes/${attrId}`,
        { params: { attr_sel: this.TabsName } }
      )
      this.editForm = res.data
    },
    // ,
    resetForm () {
      this.$refs.addForm1.resetFields()
    },
    // ,
    resetForm2 () {
      this.$refs.addForm2.resetFields()
    },
    //  ,
    addsubmitForm () {
      this.$refs.addForm1.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$axios.post(
          `categories/${this.cascaderId}/attributes`,
          { attr_name: this.addForm.attr_name, attr_sel: this.TabsName }
        )
        if (res.meta.status !== 201) {
          this.$notify.error({
            title: '',
            message: res.meta.msg,
            duration: 2000
          })
        } else {
          this.gethandleChange() // 
          this.dialogVisible = false
          this.$message.success(res.meta.msg)
        }
      })
    },
    //  ,
    editsubmitForm () {
      this.$refs.addForm2.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$axios.put(
          `categories/${this.cascaderId}/attributes/${this.editForm.attr_id}`,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.TabsName
          }
        )
        if (res.meta.status !== 200) {
          this.$notify.error({
            title: '',
            message: res.meta.msg,
            duration: 2000
          })
        } else {
          this.$message.success(res.meta.msg)
          this.dialogVisible2 = false
          this.gethandleChange()
        }
      })
    },
    //  
    async deleteOpenDialog (attrid) {
      const rulest = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (rulest === 'confirm') {
        const { data: res } = await this.$axios.delete(
          `categories/${this.cascaderId}/attributes/${attrid}`
        )
        this.$message.success(res.meta.msg)
        this.gethandleChange()
      } else {
        this.$message.info('')
      }
    },
    //  
    async setTagInputConfirm (row) {
      const { data: res } = await this.$axios.put(
        `categories/${this.cascaderId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(',')
        }
      )
      if (res.meta.status !== 200) {
        this.$notify.error({
          title: '',
          message: res.meta.msg,
          duration: 2000
        })
      } else {
        this.$message.success(res.meta.msg)
      }
    },
    // tag
    handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        // 
        row.inputValue = '' // 
        row.inputVisible = false
      } else {
        row.attr_vals.push(row.inputValue.trim()) // 
        row.inputValue = '' // 
        row.inputVisible = false
        // 
        this.setTagInputConfirm(row)
      }
    },
    // tag
    showInput (row) {
      row.inputVisible = true
      this.$nextTick(_ => {
        //  $nextTick
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // tag
    restInputConfirm (index, row) {
      row.attr_vals.splice(index, 1)
      // 
      this.setTagInputConfirm(row)
    }
  }
}
</script>

<style lang='less' scoped>
.el-cascader {
  margin: 15px 0;
  span {
    margin-right: 10px;
  }
}
.el-tag {
  margin: 0 5px;
}
.input-new-tag {
  width: 100px;
}

</style>
