<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="24">
          <el-alert title="" type="info" center show-icon :closable="false"></el-alert>
        </el-col>
        <!--  -->
        <el-col :span="24">
          <el-steps :active="activeIndex - 0" finish-status="success" align-center>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
          </el-steps>
        </el-col>
      </el-row>
      <!-- Tbs -->
      <el-form :model="TbsruleForm" :rules="rules" ref="ruleForm" label-position="top">
        <el-tabs
          :tab-position="'left'"
          v-model="activeIndex"
          :before-leave="beforeTable"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                expand-trigger="hover"
                :options="cateoptions"
                :props="cateProps"
                v-model="TbsruleForm.goods_cat"
                clearable
                @change="parentCateChange"
              ></el-cascader>
            </el-form-item>
            <el-form-item label="-(/,)" prop="goods_name">
              <el-input v-model="TbsruleForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="TbsruleForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number" >
              <el-input v-model="TbsruleForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight" >
              <el-input v-model="TbsruleForm.goods_weight" type="number"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              :label="value.attr_name"
              v-for="value in manyTbsData"
              :key="value.attr_id"
            >
              <el-checkbox-group v-model="value.attr_vals">
                <el-checkbox
                  border
                  :label="value2"
                  v-for="(value2,index) in value.attr_vals"
                  :key="index"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTbsData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              :headers="headerObj"
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture"
              style='width:500px'
              :multiple='true'
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="TbsruleForm.goods_introduce" ></quill-editor>
            <!--  -->
            <el-button type="primary" @click='add'></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import _ from 'lodash'// 
export default {
  created () {
    this.getclassification()
  },
  data () {
    return {
      activeIndex: '0',
      TbsruleForm: {
        // 
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_cat: [], // 
        pics: [], // 
        goods_introduce: '', // 
        attrs: [] // 
      },
      cateoptions: [],
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      rules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]
      },
      manyTbsData: [], // ,
      onlyTbsData: [], // 
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload', // 
      // tokenAuthorization
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      picture: '', // 
      dialogVisible: false
    }
  },
  computed: {
    cateID () {
      if (this.TbsruleForm.goods_cat.length === 3) {
        // 
        return this.TbsruleForm.goods_cat[2]
      }
      return null
    }
  },
  methods: {
    // ,
    async getclassification () {
      const { data: res } = await this.$axios.get('categories')
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.cateoptions = res.data// 
    },
    // 
    parentCateChange () {
      if (this.TbsruleForm.goods_cat.length !== 3) {
        this.TbsruleForm.goods_cat = []
      }
    },
    // Tabs next old
    beforeTable (next, old) {
      // ,
      if (old === '0' && this.TbsruleForm.goods_cat.length !== 3) {
        this.$notify.warning({
          title: '',
          message: ','
        })
        return false // 
      }
    },
    // tabs,
    async tabClicked () {
      // 
      if (this.activeIndex === '1') {
        // ,many
        const { data: res } = await this.$axios.get(
          `categories/${this.cateID}/attributes`,
          { params: { sel: 'many' } }
        )
        if (res.meta.status !== 200) {
          this.$message.error('')
        }
        res.data.forEach(item => {
          // 
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
        })
        this.manyTbsData = res.data
        // 
      } else if (this.activeIndex === '2') {
        // ,only
        const { data: res } = await this.$axios.get(
          `categories/${this.cateID}/attributes`,
          { params: { sel: 'only' } }
        )
        if (res.meta.status !== 200) {
          this.$message.error('')
        }
        this.onlyTbsData = res.data
      }
    },
    // 
    handlePreview (file) {
      this.picture = file.response.data.url
      this.dialogVisible = true
    },
    // 
    handleRemove (file) {
      const filePath = file.response.data.tmp_path // 
      const i = this.TbsruleForm.pics.findIndex(x => x.pic === filePath) // 
      this.TbsruleForm.pics.splice(i, 1) // 
    },
    // 
    handleSuccess (response) {
      const pictureUrl = {
        pic: response.data.tmp_path
      }
      // form
      this.TbsruleForm.pics.push(pictureUrl)
    },
    // ,
    add () {
      this.$refs.ruleForm.validate(async valid => {
        if (!valid) {
          return this.$notify.error({
            title: '',
            message: ''
          })
        }
        // ,
        this.manyTbsData.forEach(item => {
          const attr = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(',')
          }
          this.TbsruleForm.attrs.push(attr)
        })
        // 
        this.onlyTbsData.forEach(item => {
          const attr = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.TbsruleForm.attrs.push(attr)
        })
        //  form
        const form = _.cloneDeep(this.TbsruleForm)
        // formform
        form.goods_cat = form.goods_cat.join(',')
        //  
        const { data: res } = await this.$axios.post('goods', form)
        if (res.meta.status !== 201) {
          return this.$notify.error({
            title: '',
            message: res.meta.msg
          })
        }
        this.$message.success(res.meta.msg)
        this.activeIndex = '6'
      })
    }
  }
}
</script>

<style lang='less' scoped>
.el-checkbox {
  margin: 0 10px !important;
}
.dialog-img {
  width: 100%;
}
.quill-editor{
  min-height:300px;
  margin-bottom: 50px
}
.el-input{
  width:300px
}
</style>
