 <template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-button type="primary" style="margin-bottom:15px" @click="AddClassification"></el-button>
      <!--  -->
      <tree-table
        :data="classifyList"
        :columns="columns"
        :expand-type="false"
        border
        :selection-type="false"
        :show-index="true"
      >
        <!--   -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" style="color:lightgreen" v-if="scope.row.cat_deleted===false"></i>
          <i class="el-icon-error" style="color:red" v-else></i>
        </template>
        <!--   -->
        <template slot="tag" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level===0"></el-tag>
          <el-tag type="success" v-if="scope.row.cat_level===1"></el-tag>
          <el-tag type="warning" v-if="scope.row.cat_level===2"></el-tag>
        </template>
        <!--   -->
        <template slot="but" slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-edit"
            @click="editclassify(scope.row.cat_id)"
          ></el-button>
          <el-button
            type="danger"
            size="mini"
            icon="el-icon-delete"
            @click="deleteclassify(scope.row.cat_id)"
          ></el-button>
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-size="queryInfo.pagesize"
        :page-sizes="[1, 3, 5, 10]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--   -->
    <el-dialog title="" :visible.sync="dialogVisible" width="40%" @close="resetForm">
      <el-form ref="ClassificationForm1" :model="ClassificationForm" :rules="ClassificationRules">
        <!--   -->
        <el-form-item label=":" label-width="90px" prop="cat_name">
          <el-input v-model="ClassificationForm.cat_name"></el-input>
        </el-form-item>
        <!--  :props :options -->
        <el-form-item label=":" label-width="90px">
          <el-cascader
            expand-trigger="hover"
            :options="parentCateList"
            :props="cascaderProps"
            v-model="selectedKeys"
            clearable
            change-on-select
            @change="parentCateChange"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addForm"> </el-button>
      </span>
    </el-dialog>
    <!--   -->
    <el-dialog title="" :visible.sync="dialogVisible2" width="40%" >
      <el-form :model="editruleForm" :rules="editrules" ref="editruleForm" label-width="100px" >
        <el-form-item label="" prop='cat_name'>
          <el-input v-model='editruleForm.cat_name'></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible2 = false"> </el-button>
        <el-button type="primary" @click="submitEdit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>

export default {
  created () {
    this.getlaCssifyList()
  },
  data () {
    return {
      classifyList: [], // 
      queryInfo: {
        // 
        type: 3, // 
        pagenum: 1, // 
        pagesize: 10 // 
      },
      total: 0, // ,
      columns: [
        // tree-table
        {
          // 
          label: '', // 
          prop: 'cat_name' // 
        },
        {
          // 
          label: '', // 
          type: 'template', //  
          template: 'isok' // ,slot
        },
        {
          // 
          label: '',
          type: 'template',
          template: 'tag'
        },
        {
          // 
          label: '',
          type: 'template',
          template: 'but'
        }
      ],
      dialogVisible: false,
      dialogVisible2: false,
      ClassificationForm: {
        // 
        cat_name: '', // 
        cat_pid: 0, // id
        cat_level: 0 // 
      },
      editruleForm: {
        // 
        cat_name: '' // 
      },
      ClassificationRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editrules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      selectedKeys: [], // 
      parentCateList: [], // 
      cascaderProps: {
        // :props
        value: 'cat_id', // id
        label: 'cat_name', // 
        children: 'children' // 
      }
    }
  },
  methods: {
    // ,
    async getlaCssifyList () {
      const { data: res } = await this.$axios.get('categories', {
        params: this.queryInfo
      })
      this.classifyList = res.data.result
      this.total = res.data.total
    },
    // pagesize ,
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getlaCssifyList()
    },
    // pagenum  ,
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getlaCssifyList()
    },
    // 
    AddClassification () {
      this.dialogVisible = true
      this.getCategoriesList()
    },
    // 2
    async getCategoriesList () {
      const { data: res } = await this.$axios.get('categories', {
        params: { type: 2 }
      })
      this.parentCateList = res.data
    },
    // ,
    parentCateChange () {
      if (this.selectedKeys.length > 0) {
        // 
        this.ClassificationForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1] // id,
        this.ClassificationForm.cat_level = this.selectedKeys.length // 
      } else {
        // 
        this.ClassificationForm.cat_pid = 0 // 
        this.ClassificationForm.cat_level = 0 // 
      }
    },
    //  ,
    addForm () {
      this.$refs.ClassificationForm1.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$axios.post(
          'categories',
          this.ClassificationForm
        )
        if (res.meta.status !== 201) {
          this.$notify.error({
            title: '',
            message: res.meta.msg,
            duration: 2000
          })
        } else {
          this.$message.success(res.meta.msg)
          this.getlaCssifyList()// 
          this.dialogVisible = false
        }
      })
    },
    // ,
    resetForm () {
      this.$refs.ClassificationForm1.resetFields()
      this.selectedKeys = []
      this.ClassificationForm.cat_pid = 0 // 
      this.ClassificationForm.cat_level = 0 // 
    },
    // ,
    async deleteclassify (Id) {
      const result = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (result !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$axios.delete(`categories/${Id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getlaCssifyList()
    },
    // ,id
    async editclassify (Id) {
      this.dialogVisible2 = true
      const { data: res } = await this.$axios.get(`categories/${Id}`)
      this.editruleForm = res.data
    },
    // ,
    submitEdit () {
      this.$refs.editruleForm.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$axios.put(`categories/${this.editruleForm.cat_id}`, { cat_name: this.editruleForm.cat_name })
        this.$message.success(res.meta.msg)
        this.getlaCssifyList()
        this.dialogVisible2 = false
      })
    }
  }
}
</script>

<style lang='less' scoped>
.el-cascader {
  width: 100%;
}
</style>
