<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="6">
          <!--  -->
          <el-form ref="form" :model="searchform">
            <el-input
              placeholder=""
              v-model="searchform.query"
              clearable
              @clear="getGoodsList"
            >
              <el-button slot="append" icon="el-icon-search" @click="seachGoodsList "></el-button>
            </el-input>
          </el-form>
        </el-col>
        <el-col :span="6">
          <!--  -->
          <el-button type="primary" @click="goAddGoods"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="goodsList" border stripe style="width: 100%;margin:15px 0">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="120px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="120px"></el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">{{scope.row.add_time | date}}</template>
        </el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="editGoodsList(scope.row.goods_id)"
            ></el-button>
            <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="deleteGoodsList(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="searchform.pagenum"
        :page-size="searchform.pagesize"
        :page-sizes="[5, 10, 15, 20]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="80%" @close="resetTbsName">
      <!-- Tbs -->
      <el-form :model="editGoodsForm" :rules="editGoodsrules" ref="editGoods" label-position="top">
        <el-tabs v-model="activeName">
          <el-tab-pane label="" name="1">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="editGoodsForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="editGoodsForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="editGoodsForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="editGoodsForm.goods_weight" type="number"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in editGoodsForm.attrs"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_value"></el-input>
            </el-form-item>
             <span v-if='editGoodsForm.attrs.length===0'></span>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              :headers="headerObj"
              :action="uploadURL"
              :on-success="uploadSuccess"
              :on-remove="handleRemove"
              :on-preview="handlePreview"
              :file-list="pics"
              list-type="picture"
              :multiple="true"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor v-model="editGoodsForm.goods_introduce" style="min-height:300px"></quill-editor>
            <div class="but">
              <el-button type="primary" @click="submitEditGoods"></el-button>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created () {
    this.getGoodsList()
  },
  data () {
    return {
      searchform: {
        //  
        query: '',
        pagenum: 1, // 
        pagesize: 10 // 
      },
      goodsList: [], // 
      total: 0,
      dialogVisible: false,
      dialogVisible2: false,
      activeName: '1', // Tbs
      pics: [], // 
      picture: '', // 
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      editGoodsForm: {
        // 
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        attrs: [], // 
        pics: {}, // 
        goods_introduce: '' // 
      },
      editGoodsrules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'change' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'change' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    async getGoodsList () {
      const { data: res } = await this.$axios.get('goods', {
        params: this.searchform
      })
      if (res.meta.status !== 200) {
        this.$notify.error({
          title: '',
          message: res.meta.msg,
          duration: 2000
        })
      } else {
        this.goodsList = res.data.goods
        this.total = res.data.total
      }
    },
    // page-size
    handleSizeChange (newSize) {
      this.searchform.pagesize = newSize
      this.getGoodsList()
    },
    // pagenum
    handleCurrentChange (newPage) {
      this.searchform.pagenum = newPage
      this.getGoodsList()
    },
    // 
    seachGoodsList () {
      this.getGoodsList()
    },
    // 
    async deleteGoodsList (ID) {
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (result === 'confirm') {
        const { data: res } = await this.$axios.delete(`goods/${ID}`)
        if (res.meta.status !== 200) {
          this.$message.info('')
        }
        this.getGoodsList()
        this.$message.success(res.meta.msg)
      } else {
        this.$message.info('')
      }
    },
    // 
    goAddGoods () {
      this.$router.push('/goods/add')
    },
    // ,id,,
    async editGoodsList (Id) {
      this.dialogVisible = true
      const { data: res } = await this.$axios.get(`goods/${Id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success(res.meta.msg)
      this.editGoodsForm = res.data
      this.editGoodsForm.pics.forEach(item => {
        const p = {
          url: item.pics_big_url,
          tmp_url: item.pics_big
        }
        this.pics.push(p)
      })
    },
    // tbs
    resetTbsName () {
      this.activeName = '1'
      this.pics = []
    },
    // 
    uploadSuccess (response) {
      const picURL = {
        pic: response.data.tmp_path
      }
      // tm_pathform
      this.editGoodsForm.pics.push(picURL)
    },
    // 
    handleRemove (file) {
      const filePath = file.tmp_path
      const i = this.editGoodsForm.pics.findIndex(x => x.pic === filePath)
      this.editGoodsForm.pics.splice(i, 1)
    },
    // 
    handlePreview (file) {
      this.picture = file.response.data.url
      this.dialogVisible2 = true
    },
    // 
    submitEditGoods () {
      this.$refs.editGoods.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        const { data: res } = await this.$axios.put(
          `goods/${this.editGoodsForm.goods_id}`,
          this.editGoodsForm
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getGoodsList()
        this.dialogVisible = false
      })
    }
  }
}
</script>

<style lang='less' scoped>
.but {
  display: flex;
  justify-content: flex-end;
}
.dialog-img {
  width: 100%;
}
</style>
